import props from './props';
import value from './value';

import button from './mixin-button';
import openType from './mixin-open-type';

import { addUnit } from '../common/utils/add-unit';
import { style as styleUtil } from '../common/utils/style';
import PressIconPlus from '../press-icon-plus/press-icon-plus.vue';
import PressLink from '../press-link/press-link.vue';

let mixins = [];






mixins = [value, props];


export default {
  name: 'PressText',
  options: {
    virtualHost: true,
    styleIsolation: 'shared',
  },
  components: {
    PressIconPlus,
    PressLink,
  },
  mixins,
  data() {
    return {};
  },
  computed: {
    valueStyle() {
      const style = {
        textDecoration: this.decoration,
        fontWeight: this.bold ? 'bold' : 'normal',
        wordWrap: this.wordWrap,
        fontSize: addUnit(this.size),
      };
      !this.type && (style.color = this.color);
      this.isNvue && this.lines && (style.lines = this.lines);
      this.lineHeight
              && (style.lineHeight = addUnit(this.lineHeight));
      !this.isNvue && this.block && (style.display = 'block');

      return styleUtil([style, this.customStyle]);
    },
    parsedIconStyle() {
      const style = {
        fontSize: '15px',
        display: 'flex',
        alignItems: 'center',
      };
      return styleUtil([style, this.iconStyle]);
    },
    isNvue() {
      let nvue = false;



      return nvue;
    },
    isMp() {
      let mp = false;



      return mp;
    },
  },
  methods: {
    styleUtil,
    clickHandler() {
      // 如果为手机号模式，拨打电话
      if (this.call && this.mode === 'phone') {
        uni.makePhoneCall({
          phoneNumber: this.text,
        });
      }
      this.$emit('click');
    },
  },
};
